#demo {
	section {
		margin: 60px auto; } }

body {
	font-family: arial; }

.navigation {
	display: flex;
	background: rgba(0,0,0,0.65);
	color: white;
	position: fixed;
	width: 100%;
	margin-top: 0px;
	padding: 0.5%;
	z-index: 10;
	.logo {
		margin-left: 2%;
		a {
			text-decoration: none;
			color: white;
			font-size: 22px; } }

	.nav {
		justify-content: flex-end; } }

ul.nav {
	display: flex;
	position: absolute;
	right: 0px;
	li {
		list-style-type: none;
		margin: 0% 1%;
		a {
			text-decoration: none;
			color: white;
			margin-right: 50px; }
		a:visited {
			text-decoration: underline; } } }

ul.nav li:hover {
	text-decoration: underline; }

.header {
	height: 50vh;
	background-image: url('images/headerImage.jpg');
	background-position:: 5px 815px;
	background-size: cover;
	height: 600px;
	position: relative;
	h1 {
		font-size: 40px;
		color: white;
		font-weight: lighter;
		width: 55%;
		margin: 0 auto;
		margin-top: 115px;
		margin-bottom: 0px; }
	h2 {
		margin: 0 auto;
		width: 50%;
		font-size: 60px;
		color: #fff;
		font-weight: lighter; } }

.inner {
	text-align: center;
	padding-top: 45px;
	position: absolute;
	background: rgba(69,40,130,0.85);
	top: 0;
	bottom: 0;
	left: 0;
	right: 0; }

.videoImage {
	position: absolute;
	background: white;
	border: 60px solid white;
	border-radius: 50px;
	margin-left: 22.5%;
	margin-top: 25%; }

.about {
	position: relative;
	color: #5a5a5a;
	line-height: 30px;
	display: flex;
	margin: 0 auto;
	font-size: 16px;
	width: 85%;
	margin-bottom: 0px;
	.leftCollumn {
		padding: 5%;
		width: 50%;
		padding-top: 0px; }
	.rightCollumn {
		padding: 5%;
		width: 50%;
		padding-top: 0px; } }

.footer {
	background: black;
	color: white;
	display: flex;
	padding: 2%;
	position: relative; }

.footer i {
	font-size: 2rem;
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	border-radius: 50%;
	margin: 0 5px;
	border: 1px solid white; }

.footer i.fa.fa-instagram {
 }	// :padding 12%

.footer i.fa.fa-facebook {
	// :padding 16%
 }	// :font-size 1.95em

.footer .logo p {
	font-size: 1.5em; }

.footer ul.socialMedia {
	position: absolute;
	right: 5%; }

.footer ul li {
	list-style-type: none; }

.footer ul li a {
	text-decoration: none;
	color: white; }

.socialMedia {
