//SLIDER

.flickity-viewport {
	height: 400px; }

.flickity-prev-next-button .arrow {
	fill: white !important; }

.flickity-prev-next-button {
	background: hsla(0,0%,0%,0) !important; }

.carousel-cell {
	width: 100%;
	height: 300px; }

.inner-cell {
	display: flex;
	.cell-left {
		width: 70%; }
	.cell-right {
		width: 30%;
		img {
			height: 260px;
			transform: rotate(3deg);
			box-shadow: 3px 3px 50px;
			margin-top: 20px; } } }

.cellHeader {
	font-size: 24px;
	font-weight: bold;
	width: 70%;
	margin: 0 auto;
	text-align: center;
	line-height: 30px;
	margin-top: 45px; }

.cellSub {
	margin: 0 auto;
	width: 50%;
	text-align: center;
	margin-top: 15px;
	font-size: 20px; }

.cellButton {
	background: #FF7E60;
	text-decoration: none;
	color: white;
	font-size: 15px;
	padding: 20px;
	margin: 0 auto;
	display: block;
	width: 50%;
	text-align: center;
	margin-top: 20px;
	box-shadow: 1px 1px 5px black; }


.cellOne {
	background: #89CBE0; }

.cellTwo {
	background: #9197E5;
	.cellButton {
		background: black; } }

.cellThree {
	background: #7BCA68;
	.cellButton {
		background: #186340; } }

//VIDEO SLIDER

.fancybox-content {
