//TAB SECTION

/* Style the tab */
div.tab {
	overflow: hidden;
	background-color: #3B4254; }

.tablinks {
	color: #FFFFFF; }

h3 {
	color: #FFFFFF;
	font-size: 32px;
	margin-bottom: 0px; }

.tabContent p {
	font-size: 16px; }

/* Style the buttons inside the tab */
div.tab button {
	background-color: #5F6A87;
	float: left;
	border: none;
	outline: none;
	cursor: pointer;
	padding: 25px 16px;
	transition: 0.3s;
	width: 16.6%;
	border-left: 1px solid #3B4254; }


/* Change background color of buttons on hover */
div.tab button:hover {
	background-color: #ddd; }


/* Create an active/current tablink class */
div.tab button.active {
	background-color: #3B4254; }

.videoFlex a {
	padding-right: 95px;
	padding-bottom: 50px;
	img {
		width: 120%; } }
/* Style the tab content */
.tabcontent {
	display: none;
	padding: 6px 12px;
	border-top: none;
	background: #3B4254;
	color: #AFBCC4;
	p {
		line-height: 25px; } }


.videoFlex {
	display: flex; }

